import React, { useState } from 'react';
import axios from 'axios';

export default function Gemini() {
  const [question, setQuestion] = useState('');
  const [answer, setAnswer] = useState('');
  const [loading, setLoading] = useState(false);
  const [copied, setCopied] = useState(false);

  const API_KEY = 'AIzaSyAYbj5Mt8-REAdlnbWosTGJPXrVNd-9pEg';
  const url = `https://generativelanguage.googleapis.com/v1/models/gemini-1.5-flash:generateContent?key=${API_KEY}`;

  const handleAsk = async () => {
    if (!question.trim()) return;
    setLoading(true);
    setAnswer('');
    setCopied(false);

    const body = {
      contents: [
        {
          parts: [{ text: question }],
        },
      ],
    };

    try {
      const res = await axios.post(url, body);
      const output =
        res.data.candidates?.[0]?.content?.parts?.[0]?.text || 'No answer.';
      setAnswer(output);
    } catch (err) {
      console.error(err);
      setAnswer(
        'Error: ' + (err.response?.data?.error?.message || err.message)
      );
    } finally {
      setLoading(false);
    }
  };

  const handleCopy = () => {
    if (!answer) return;
    navigator.clipboard.writeText(answer);
    setCopied(true);
    setTimeout(() => setCopied(false), 2000);
  };

  const handleClear = () => {
    setQuestion('');
    setAnswer('');
    setCopied(false);
  };

  return (
    <div
      style={{
        maxWidth: 700,
        margin: '40px auto',
        padding: 24,
        borderRadius: 10,
        boxShadow: '0 8px 24px rgba(0,0,0,0.06)',
        fontFamily: 'Segoe UI, Tahoma, sans-serif',
        backgroundColor: '#fdfdfd',
        border: '1px solid #e0e0e0',
      }}
    >
      <h2
        style={{
          textAlign: 'center',
          color: '#2c3e50',
          fontSize: '26px',
          marginBottom: '20px',
        }}
      >
        Ask Gemini AI
      </h2>

      <textarea
        value={question}
        onChange={(e) => setQuestion(e.target.value)}
        placeholder='Type your question here...'
        rows={4}
        disabled={loading}
        style={{
          width: '100%',
          padding: '12px',
          fontSize: '16px',
          borderRadius: '8px',
          border: '1.5px solid #ccc',
          resize: 'vertical',
          backgroundColor: loading ? '#f1f1f1' : '#fff',
          color: '#333',
          boxSizing: 'border-box',
        }}
      />

      <div
        style={{
          marginTop: '16px',
          display: 'flex',
          flexWrap: 'wrap',
          gap: '12px',
        }}
      >
        <button
          onClick={handleAsk}
          disabled={loading || !question.trim()}
          style={{
            flex: 1,
            minWidth: 120,
            padding: '12px',
            backgroundColor: loading ? '#95a5a6' : '#2ecc71',
            color: '#fff',
            fontSize: '16px',
            border: 'none',
            borderRadius: '6px',
            cursor: loading ? 'not-allowed' : 'pointer',
            fontWeight: 'bold',
            transition: 'background 0.3s ease',
          }}
        >
          {loading ? 'Thinking...' : 'Ask'}
        </button>

        <button
          onClick={handleClear}
          disabled={loading || (!question && !answer)}
          style={{
            flex: 1,
            minWidth: 120,
            padding: '12px',
            backgroundColor: '#e74c3c',
            color: '#fff',
            fontSize: '16px',
            border: 'none',
            borderRadius: '6px',
            cursor: 'pointer',
            fontWeight: 'bold',
            transition: 'background 0.3s ease',
          }}
        >
          Clear
        </button>
      </div>

      {answer && (
        <div
          style={{
            marginTop: 28,
            backgroundColor: '#fff',
            padding: '18px',
            borderRadius: '8px',
            border: '1px solid #ddd',
            position: 'relative',
            whiteSpace: 'pre-wrap',
            fontSize: '16px',
            color: '#333',
          }}
        >
          <strong style={{ display: 'block', marginBottom: 10 }}>
            Answer:
          </strong>
          {answer}
          <button
            onClick={handleCopy}
            style={{
              position: 'absolute',
              top: 10,
              right: 10,
              backgroundColor: copied ? '#27ae60' : '#3498db',
              color: '#fff',
              border: 'none',
              borderRadius: '5px',
              padding: '6px 10px',
              cursor: 'pointer',
              fontSize: '14px',
              transition: 'background-color 0.3s ease',
            }}
            title='Copy answer'
          >
            {copied ? 'Copied!' : 'Copy'}
          </button>
        </div>
      )}
    </div>
  );
}
